"use client";

import { useCallback, useState } from "react";
import { useAccount, useConnect, useSwitchChain, useWalletClient } from "wagmi";
import { ogTestnet } from "@/lib/wagmiConfig";

type Props = {
  onSuccess: (address: string) => void;
};

export function DeployOnPage({ onSuccess }: Props) {
  const { address, isConnected, chainId } = useAccount();
  const { connect, connectors, isPending } = useConnect();
  const { switchChain, isPending: isSwitching } = useSwitchChain();
  const { data: walletClient } = useWalletClient();
  const [status, setStatus] = useState<"idle" | "loading" | "success" | "error">("idle");
  const [error, setError] = useState<string | null>(null);
  const [deployedAddress, setDeployedAddress] = useState<string | null>(null);

  const needsNetworkSwitch = isConnected && chainId !== ogTestnet.id;

  const handleDeploy = useCallback(async () => {
    if (!walletClient) {
      setError("Wallet not connected");
      return;
    }
    if (chainId !== ogTestnet.id) {
      setError("Switch to 0G Testnet in your wallet first.");
      return;
    }
    setStatus("loading");
    setError(null);
    try {
      const res = await fetch("/contracts/HumanAgentINFT.json");
      const { abi, bytecode } = (await res.json()) as { abi: unknown[]; bytecode: `0x${string}` };
      const hash = await walletClient.deployContract({
        abi,
        bytecode,
        args: [],
      });
      const receipt = await walletClient.waitForTransactionReceipt({ hash });
      const addr = receipt.contractAddress;
      if (addr) {
        setDeployedAddress(addr);
        setStatus("success");
        onSuccess(addr);
      } else {
        throw new Error("No contract address in receipt");
      }
    } catch (err: unknown) {
      const msg = err instanceof Error ? err.message : String(err);
      setError(msg);
      setStatus("error");
    }
  }, [walletClient, onSuccess, chainId]);

  const metaMask = connectors.find((c) => c.name?.toLowerCase().includes("metamask"));
  const injected = connectors.find((c) => c.type === "injected") ?? metaMask;

  return (
    <div className="rounded-xl border border-white/15 bg-white/5 p-4">
      <p className="mb-3 text-sm text-white/80">
        Connect your wallet and deploy HumanAgentINFT. You&apos;ll need testnet tokens for gas.
      </p>
      {!isConnected ? (
        <div>
          <button
            type="button"
            onClick={() => injected && connect({ connector: injected })}
            disabled={!injected || isPending}
            className="btn-primary rounded-lg px-4 py-2 text-sm font-medium"
          >
            {isPending ? "Connecting..." : "Connect Wallet"}
          </button>
          {!injected && (
            <p className="mt-2 text-xs text-amber-300/90">Install MetaMask or another injected wallet.</p>
          )}
        </div>
      ) : (
        <div className="space-y-3">
          <p className="text-xs text-white/60">Connected: {address?.slice(0, 10)}...{address?.slice(-8)}</p>
          {needsNetworkSwitch ? (
            <div>
              <p className="mb-2 text-sm text-amber-200/90">Switch your wallet to 0G Testnet to deploy.</p>
              <button
                type="button"
                onClick={() => switchChain({ chainId: ogTestnet.id })}
                disabled={isSwitching}
                className="btn-primary rounded-lg px-4 py-2 text-sm font-medium"
              >
                {isSwitching ? "Switching..." : "Switch to 0G Testnet"}
              </button>
            </div>
          ) : status === "success" && deployedAddress ? (
            <div className="rounded-lg border border-emerald-400/30 bg-emerald-500/10 p-3">
              <p className="text-sm font-medium text-emerald-200">Deployed!</p>
              <code className="mt-1 block break-all text-xs text-emerald-100/90">{deployedAddress}</code>
            </div>
          ) : (
            <button
              type="button"
              onClick={handleDeploy}
              disabled={status === "loading"}
              className="btn-primary rounded-lg px-4 py-2 text-sm font-medium"
            >
              {status === "loading" ? "Deploying..." : "Deploy HumanAgentINFT"}
            </button>
          )}
          {status === "error" && error && (
            <p className="text-xs text-red-300">{error}</p>
          )}
        </div>
      )}
    </div>
  );
}
